import { Injectable, Inject, PipeTransform, EventEmitter, Output } from '@angular/core';
import { HttpClient, HttpEvent, HttpHeaders, HttpErrorResponse, HttpEventType } from '@angular/common/http';

import { SearchResult } from './models/search-result.model';
import { State } from './models/state.model';
import { BehaviorSubject, Observable, of, Subject } from 'rxjs';
import { map, catchError, debounceTime, delay, switchMap, tap } from 'rxjs/operators';

import { Arquivo } from './models/arquivo.model';
import { SortDirection } from './directives/sortable.directive';
import { faFileArchive, faFileCsv, faFilePowerpoint, faFileWord, faFileExcel, faFileImage, faFileAlt, faFilePdf, faFileCode, faFile, IconDefinition } from '@fortawesome/free-solid-svg-icons';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};


function compare(v1, v2) {
  return v1 < v2 ? -1 : v1 > v2 ? 1 : 0;
}

function sort<T>(items: Arquivo[], column: string, direction: string): Arquivo[] {
  if (direction === '') {
    return items;
  } else {
    return [...items].sort((a, b) => {
      const res = compare(a[column], b[column]);
      return direction === 'asc' ? res : -res;
    });
  }
}

function matches(item: Arquivo, text: string, pipe: PipeTransform) {
  const term = text.toLowerCase();
  return item.nome.includes(term);
}

@Injectable({
  providedIn: 'root'
})
export class UpdownloadService {

  @Output() ApiResponse = new EventEmitter();
  private serviceUrl = 'arquivo';
  private token: string = "";
  private downloadAPI = {
    url: "/conppwPadraoC9-war/ArquivoDownloadServlet",
    headers: {
      "Content-Type": "text/plain;charset=UTF-8",
      "Authorization": `Bearer ${this.token}`
    }
  };
  private listaDownloadedItems: Arquivo[] = [];
  private listaUploadedItems: Arquivo[] = [];
  private _progressLoading$ = new BehaviorSubject<boolean>(true);
  private _progressMessage$ = new BehaviorSubject<{}>({ type: null, msg: null });
  private _percentComplete$ = new BehaviorSubject<number>(0);
  private _loading$ = new BehaviorSubject<boolean>(true);
  private _searchDownloaded$ = new Subject<void>();
  private _searchUploaded$ = new Subject<void>();
  private _uploadedItems$ = new BehaviorSubject<Arquivo[]>([]);
  private _downloadedItems$ = new BehaviorSubject<Arquivo[]>([]);
  private _total$ = new BehaviorSubject<number>(0);
  private _serviceName = "UpdownloadService";

  private _state: State = {
    page: 1,
    pageSize: 1000,
    codigo: 'in',
    searchTerm: '',
    sortColumn: '',
    sortDirection: ''
  };

  constructor(private http: HttpClient) {
    this.getItems("out").subscribe(items => {
      items.forEach(element => {
        element.tamanhoString = this.convertSize(element.tamanho);
        element.type = this.identifyIcon(element.nome);
        element.folder = "out";
      });
      this.listaDownloadedItems = items;
      this._downloadedItems$.next(items);
    });
    this.getItems("in").subscribe(items => {
      items.forEach(element => {
        element.tamanhoString = this.convertSize(element.tamanho);
        element.type = this.identifyIcon(element.nome);
        element.folder = "in";
      });
      this.listaUploadedItems = items;
      this._uploadedItems$.next(items);
    });
    this._searchDownloaded$.pipe(
      tap(() => this._loading$.next(true)),
      debounceTime(200),
      switchMap(() => this._searchDownloaded()),
      delay(200),
      tap(() => this._loading$.next(false))
    ).subscribe(result => {
      this._downloadedItems$.next(result.items);
      this._total$.next(result.total);
    });
    this._searchDownloaded$.next();
    this._searchUploaded$.pipe(
      tap(() => this._loading$.next(true)),
      debounceTime(200),
      switchMap(() => this._searchUploaded()),
      delay(200),
      tap(() => this._loading$.next(false))
    ).subscribe(result => {
      this._uploadedItems$.next(result.items);
      this._total$.next(result.total);
    });
    this._searchUploaded$.next();
  }

  get downloadedItems$() { return this._downloadedItems$.asObservable(); }
  get uploadedItems$() { return this._uploadedItems$.asObservable(); }
  get total$() { return this._total$.asObservable(); }
  get loading$() { return this._loading$.asObservable(); }
  get progressLoading$() { return this._progressLoading$.asObservable(); }
  get progressMessage$() { return this._progressMessage$.asObservable(); }
  get percentComplete$() { return this._percentComplete$.asObservable(); }
  get page() { return this._state.page; }
  get pageSize() { return this._state.pageSize; }
  get codigo() { return this._state.codigo; }
  get searchTerm() { return this._state.searchTerm; }

  set page(page: number) { this._set({ page }); }
  set pageSize(pageSize: number) { this._set({ pageSize }); }
  set codigo(codigo: string) { this._set({ codigo }); }
  set searchTerm(searchTerm: string) { this._set({ searchTerm }); }
  set sortColumn(sortColumn: string) { this._set({ sortColumn }); }
  set sortDirection(sortDirection: SortDirection) { this._set({ sortDirection }); }

  private _set(patch: Partial<State>) {
    Object.assign(this._state, patch);
    this._searchUploaded$.next();
    this._searchDownloaded$.next();
  }

  private _searchDownloaded(): Observable<SearchResult<Arquivo>> {
    const { sortColumn, sortDirection, pageSize, page, searchTerm } = this._state;

    // 1. sort
    let items = sort(this.listaDownloadedItems, sortColumn, sortDirection);

    // 2. filter
    items = items.filter(item => matches(item, searchTerm, null));
    const total = items.length;

    // 3. paginate
    items = items.slice((page - 1) * pageSize, (page - 1) * pageSize + pageSize);

    //this.messageService.add("success", this._serviceName + ": Lista de items selecionados");

    return of({ items, total });

  }
  private _searchUploaded(): Observable<SearchResult<Arquivo>> {
    const { sortColumn, sortDirection, pageSize, page, searchTerm } = this._state;

    // 1. sort
    let items = sort(this.listaUploadedItems, sortColumn, sortDirection);

    // 2. filter
    items = items.filter(item => matches(item, searchTerm, null));
    const total = items.length;

    // 3. paginate
    items = items.slice((page - 1) * pageSize, (page - 1) * pageSize + pageSize);

    //this.messageService.add("success", this._serviceName + ": Lista de items selecionados");

    return of({ items, total });

  }

  public uploadItem(data: any, userId: string) {
    let uploadURL = `${this.serviceUrl}/upload`;

    return this.http.post<any>(uploadURL, data, {
      reportProgress: true,
      observe: 'events'
    }).pipe(map((event) => {

      switch (event.type) {

        case HttpEventType.UploadProgress:
          const progress = Math.round(100 * event.loaded / event.total);
          return { status: 'progress', message: progress };

        case HttpEventType.Response:
          return event.body;
        default:
          return `Unhandled event: ${event.type}`;
      }
    })
    );
  }

  getItems(type: string): Observable<Arquivo[]> {
    //this.messageService.add("info", this._serviceName + " Lista de items selecionados");
    console.log(this._serviceName + " Lista de items selecionados");
    return this.http.get<Arquivo[]>(`${this.serviceUrl}/${type}`)
      .pipe(tap(_ => console.log(`fetched items/${type}`)),
        catchError(this.handleError('getItems', []))
      );

  }

  putItem(item: Arquivo, keyName: string): Observable<any> {
    //return this.http.put(`${this.serviceUrl}/${item[keyName]}`, item, httpOptions).pipe(
    return this.http.put(`${this.serviceUrl}`, item, httpOptions).pipe(
      tap(_ => {
        console.log(`${this._serviceName}: Item cÃ³digo=${item[keyName]} atualizada com sucesso!`);
        this.listaUploadedItems.push(item);
      }),
      catchError(this.handleError<any>(`updateItem cÃ³digo=${item[keyName]}`))
    );
  }

  /** DELETE: delete item from the server */
  deleteItem(item: Arquivo, keyName?: string): Observable<Arquivo> {
    const id = typeof item === 'number' ? item : item[keyName];
    return this.http.delete<Arquivo>(`${this.serviceUrl}/${id}`, httpOptions).pipe(
      tap(_ => {
        this.listaUploadedItems.unshift(item);
        console.log(`item removido com cÃ³digo=${id}`);
      }),
      catchError(this.handleError<Arquivo>('deleteItem'))
    );
  }

  searchItem(term: string, grupo: string): Observable<Arquivo[]> {
    if (!term.trim()) {
      // if not search term, return empty hero array.
      return this.getItems(grupo);
    }
    //const url = `${this.httpUrl}/${this.serviceUrl}/?term=${term}`;
    const url = `${this.serviceUrl}/${grupo}/${term}`;
    return this.http.get<Arquivo[]>(url).pipe(
      tap(_ => console.log(_)),
      catchError(this.handleError<Arquivo[]>('searchItem', []))
    );
    //tap(_ =>console.log(`found items matching "${term}"`)),
  }
  removeProgress(){
    this._progressLoading$.next(false);
  }
  
  downloadItem(content: Arquivo) {
    if (!content.nome.trim()) {
      console.log('Arquivo invÃ¡lido');
      this._progressMessage$.next({ type: "danger", msg: `Arquivo [${content.nome}] estÃ¡ invÃ¡lido!` });
      return;
    }

    let i: any;
    this._percentComplete$.next(0);
    this._progressMessage$.next({ type: "info", msg: `Iniciando Download do arquivo ${content.nome}!` });
    this._progressLoading$.next(true);

    let isError = false;
    let xhr = new XMLHttpRequest();

    xhr.onreadystatechange = evnt => {
      //console.log("onready");
      if (xhr.readyState === 4) {
        if (xhr.status !== 200 && xhr.status !== 201) {
          isError = true;
          this._progressMessage$.next({ type: "danger", msg: `Ocorreu uma falha durante o download do arquivo ${content.nome}!` });
          setTimeout(this.removeProgress, 3000);
          //this.afterDownload = true;
          //console.log(this.uploadMsgText);
          //console.log(evnt);
        }
        this.ApiResponse.emit(xhr);
      }
    };

    xhr.onprogress = evnt => {
      if (evnt.lengthComputable) {
        this._percentComplete$.next(Math.round((evnt.loaded / evnt.total) * 100));
      }
      //console.log("Progress..."/*+this.percentComplete+" %"*/);
    };

    xhr.onload = evnt => {
      //console.log("onload");
      //console.log(evnt);
      //this.progressBarShow = false;
      if (!isError) {
        let blob = xhr.response;
        let fileName = null;
        let contentType = xhr.getResponseHeader("content-type");

        if (xhr.getResponseHeader("content-disposition")) {
          let contentDisposition = xhr.getResponseHeader("content-disposition");
          let charSearch = (contentDisposition.lastIndexOf("/") > 0) ? "/" : "=";
          fileName = contentDisposition.substring(contentDisposition.lastIndexOf(charSearch) + 1);
        } else if (contentType != null) {
          fileName = "unnamed." + contentType.substring(contentType.indexOf("/") + 1);
        } else {
          fileName = content.nome;
        }
        if (window.navigator.msSaveOrOpenBlob) {
          // Internet Explorer
          window.navigator.msSaveOrOpenBlob(new Blob([blob], { type: contentType }), fileName);
        } else {
          let link = document.createElement('a');
          link.href = window.URL.createObjectURL(blob);
          link.download = fileName;
          link.click();
        }

        this._progressMessage$.next({ type: "success", msg: `O download do arquivo ${content.nome} foi realizado com sucesso!` });
        console.log(`Sucesso -> file: ${content.nome}`);
        setTimeout(this.removeProgress, 3000);
        //this._progressLoading$.next(false);
      }
    };

    xhr.onerror = evnt => {
      //console.log("onerror");
      //console.log(evnt);
    };

    const url = this.prepareUrl({ "fileName": content.nome, "folder": content.folder });

    xhr.open("GET", url, true);
    for (const key of Object.keys(this.downloadAPI.headers)) {
      // Object.keys will give an Array of keys
      xhr.setRequestHeader(key, this.downloadAPI.headers[key]);
    }
    xhr.responseType = "blob";
    //let token = sessionStorage.getItem("token");
    //xhr.setRequestHeader("Content-Type", "text/plain;charset=UTF-8");
    //xhr.setRequestHeader('Authorization', `Bearer ${token}`);
    xhr.send();
  }

  private prepareUrl(params: any) {
    return this.downloadAPI.url + "?" + Object.keys(params).map(function (key) { return key + "=" + encodeURIComponent(params[key]) }).join("&");
  }

  private convertSize(fileSize: number) {
    //console.log(fileSize + " - "+ str);
    return fileSize < 1024000
      ? (fileSize / 1024).toFixed(2) + " KB"
      : (fileSize / 1024000).toFixed(2) + " MB";
  }

  private identifyIcon(file: string): IconDefinition {
    let fileNameItems = file.split(".");
    switch (fileNameItems[fileNameItems.length - 1]) {
      case "pdf":
        return faFilePdf;
        break;
      case "txt":
        return faFileAlt;
        break;
      case "png":
      case "jpeg":
      case "jpg":
        return faFileImage;
        break;
      case "xlsx":
      case "xls":
        return faFileExcel;
        break;
      case "docx":
      case "doc":
        return faFileWord;
        break;
      case "ppt":
      case "pptx":
        return faFilePowerpoint;
        break;
      case "csv":
        return faFileCsv;
        break;
      case "zip":
      case "rar":
        return faFileArchive;
        break;
      case "ear":
      case "war":
      case "jar":
        return faFileCode;
        break;
      default:
        return faFile;
        break;
    }
  }
	/**
	 * Handle Http operation that failed.
	 * Let the app continue.
	 * @param operation - name of the operation that failed
	 * @param result - optional value to return as the observable result
	 */
  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {

      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead

      // TODO: better job of transforming error for user consumption
      console.log(`${operation} failed: ${error.message}`);

      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}
