
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule, HttpClient }    from '@angular/common/http';
import { TranslateModule, TranslateLoader } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';

import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { ToastrModule } from 'ngx-toastr';

import { UpdownloadComponent } from './updownload.component';

import { NgbdSortableHeader } from './directives/sortable.directive';
import { AngularFileUploaderComponent } from './file-uploader/angular-file-uploader.component';
import { environment } from '../environments/environment';

//const localeStartingURL = environment.localeUrlFolder;
//const localeFileType = environment.localeFileType;

// AoT requires an exported function for factories
export function HttpLoaderFactory(http: HttpClient) {
  return new TranslateHttpLoader(http, environment.localeUrlFolder, environment.localeFileType);
}

@NgModule({
  declarations: [
    UpdownloadComponent,
    AngularFileUploaderComponent,
    NgbdSortableHeader],
  imports: [
    RouterModule.forChild([{ path: '', component: UpdownloadComponent }]),
    CommonModule,
    FontAwesomeModule,
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule,
    NgbModule,
    ToastrModule.forRoot() ,
    TranslateModule.forRoot({
          loader: {
              provide: TranslateLoader,
              useFactory: HttpLoaderFactory,
              deps: [HttpClient]
          }
      })
  ],
  exports: [UpdownloadComponent],
  bootstrap: [UpdownloadComponent]
})
export class UpdownloadModule{}
