import { Injectable, EventEmitter, Output } from '@angular/core';
import { HttpClient, HttpEvent, HttpHeaders, HttpEventType, HttpParams, HttpRequest } from '@angular/common/http';

import { BehaviorSubject, Observable, of, Subject } from 'rxjs';
import { map, catchError, tap, last } from 'rxjs/operators';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

@Injectable({
  providedIn: 'root'
})
export class UploaderService {
  @Output()
  ApiResponse = new EventEmitter();

  maxSize: number = 10;
  token: string = "";
  uploadAPI: string = "/conppwPadraoC9-war/ArquivoUploadServlet";
  formatsAllowed: string;
  formatsAllowedArray: Array<string> = [".jpg,.png,.pdf,.xls,.csv,.doc,.docx,.txt,.zip"];
  multiple: boolean = true;
  hideResetBtn: boolean;
  hideSelectBtn: boolean;
  idDate: number = +new Date();
  reg: RegExp = /(?:\.([^.]+))?$/;

  singleFile = true;''
  progressBarShow = false;
  uploadBtn = false;
  uploadMsg = false;
  progressLoading: boolean = false;
  percentComplete: number = 0;
  dropfire = false;
  
  _afterUpload = false;

  private selectedFiles: Array<File> = [];
  private uploadedFiles: Array<UploadResult> = [];
  private notAllowedList: Array<Object> = [];

  total: number = 0;
  private _loading$ = new BehaviorSubject<boolean>(true);
  private _lastMessage$ = new BehaviorSubject<string>("");
  private _selectedFiles$ = new BehaviorSubject<Array<any>>([]);
  private _notAllowedList$ = new BehaviorSubject<Array<Object>>([]);
  private _uploadedFiles$ = new BehaviorSubject<Array<UploadResult>>([]);
  
  private _progressLoading$ = new BehaviorSubject<boolean>(true);
  private _progressMessage$ = new BehaviorSubject<{}>({ type: null, msg: null });
  private _percentComplete$ = new BehaviorSubject<number>(0);
  private _total$ = new BehaviorSubject<number>(0);


  constructor(private httpClient: HttpClient) {

   }

  get selectedFiles$() { return this._selectedFiles$.asObservable(); }
  get notAllowedList$() { return this._notAllowedList$.asObservable(); }
  get uploadedFiles$() { return this._uploadedFiles$.asObservable(); }
  get total$() { return this._total$.asObservable(); }
  get loading$() { return this._loading$.asObservable(); }
  get progressLoading$() { return this._progressLoading$.asObservable(); }
  get progressMessage$() { return this._progressMessage$.asObservable(); }
  get percentComplete$() { return this._percentComplete$.asObservable(); }
  get afterUpload() { return this._afterUpload; }

  
  afterUploadAction() {
    this.resetSelectedFiles();
    this.resetNotAllowedList();
    this.resetTotalItems();
    this._afterUpload = false;
  }

  resetFileUpload() {
    this.resetSelectedFiles();
    this.resetUploadedFiles();
    this.resetNotAllowedList();
    this.resetTotalItems();
  }

  resetTotalItems(){
    this.total = this.selectedFiles.length + this.uploadedFiles.length + this.notAllowedList.length;
    this._total$.next(this.total);
  }

  resetNotAllowedList(){
    this.notAllowedList = [];
    this._notAllowedList$.next(this.notAllowedList);
  }

  resetSelectedFiles(){
    this.selectedFiles = [];
    this._selectedFiles$.next(this.selectedFiles);
  }

  resetUploadedFiles(){
    this.uploadedFiles = [];
    this._uploadedFiles$.next(this.uploadedFiles);
  }

  includeNotAllowed(item: Object) { 
    return this.notAllowedList.push(item); 
  }

  includeSelectedFile(fileItem: File) { 
    this.selectedFiles.push(fileItem); 
    this._selectedFiles$.next(this.selectedFiles);
    this.total = this.selectedFiles.length + this.uploadedFiles.length + this.notAllowedList.length;
  }

  removeSelectedFile(i: any) {
    //console.log("remove file clicked " + i)
    this.selectedFiles.splice(i, 1);
    this._selectedFiles$.next(this.selectedFiles);
    this.total = this.selectedFiles.length + this.uploadedFiles.length + this.notAllowedList.length;
  }

  public uploadSelected() {
    for (let index = 0; index < this.selectedFiles.length; index++) {
      this.uploadItem(this.selectedFiles[index]).subscribe((resultItem) => {
        this.uploadedFiles.push(resultItem);
        this._uploadedFiles$.next(this.uploadedFiles);
        this.selectedFiles.splice(index,1);
        this._selectedFiles$.next(this.uploadedFiles);
        this.total = this.selectedFiles.length + this.uploadedFiles.length + this.notAllowedList.length;
      });
    }
  }

  private uploadItem(fileItem: File): Observable<any> {
    let formData = new FormData();
    formData.append('upload', fileItem);
    const options = {
      reportProgress: true,
      params: new HttpParams()
        .set('folder', 'in')
        .set('fileName', fileItem.name)
    };
    
    //let url = this.uploadAPI + "?" + Object.keys(formData).map(function (key) { return key + "=" + encodeURIComponent(formData[key]) }).join("&");

    const req = new HttpRequest('POST', this.uploadAPI, formData, options);

    
    return this.httpClient
      .request(req).pipe(
        map(event => this.getEventMessage(event, fileItem)),
        tap(message => this.showProgress(message)),
        last(),
        catchError(this.handleError(fileItem))
      );
  }

  private showProgress(message: string){
    this._lastMessage$.next(message);
  }
  
  private getEventMessage(event: HttpEvent<any>, file: File) {
    switch (event.type) {
      case HttpEventType.Sent:
        return `Uploading file "${file.name}" of size ${file.size}.`;
      case HttpEventType.UploadProgress:
        // Compute and show the % done:
        this.percentComplete = Math.round(100 * event.loaded / event.total);
        console.log("Progress..."/*+this.percentComplete+" %"*/);
        return `File "${file.name}" is ${this.percentComplete}% uploaded.`;
      case HttpEventType.Response:
          this.progressBarShow = false;
          this.uploadBtn = false;
          this.uploadMsg = true;
          this._afterUpload = true;  
          this.ApiResponse.emit(event);
          if (event.ok) {
            console.log(`O envio do arquivo ${file.name} foi finalizado com sucesso!`);
            return `O envio do arquivo ${file.name} foi finalizado com sucesso!`;
          }else{
            console.log("Ocorreu uma falha durante o envio dos arquivos ao servidor!");
            return `Ocorreu uma falha durante o envio do arquivo "${file.name}" no tamanho ${file.size} ao servidor!`;
          }
      default:
        return `File "${file.name}" surprising upload event: ${event.type}.`;
    }
  }

  convertSize(fileSize: number) {
    //console.log(fileSize + " - "+ str);
    return fileSize < 1024000
      ? (fileSize / 1024).toFixed(2) + " KB"
      : (fileSize / 1024000).toFixed(2) + " MB";
  }
  
  private handleError<File>(result?: File) {
    return (error: any): Observable<File> => {

      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead

      // TODO: better job of transforming error for user consumption
      console.log(`Upload failed: ${error.message}`);

      // Let the app keep running by returning an empty result.
      return of(result as File);
    };
  }
}

export interface UploadResult {
  name: string;
  type: string;
  size: number;
  url: string;
}
