import { Component, OnInit, Input, Output, EventEmitter, OnChanges, SimpleChanges, Inject, ViewEncapsulation, PipeTransform } from '@angular/core';
import { faTimes, faFolderOpen, faSearch, faPaperPlane } from '@fortawesome/free-solid-svg-icons';
import { Observable, Subject, BehaviorSubject, of } from 'rxjs';
import { switchMap, delay, tap, debounceTime } from 'rxjs/operators';
import { ToastrService } from 'ngx-toastr';

import { HttpClient, HttpParams } from '@angular/common/http';
import { UploaderService } from './uploader.service';
import { Arquivo } from '../models/arquivo.model';
import { UpdownloadService } from '../updownload.service';

function compare(v1, v2) {
  return v1 < v2 ? -1 : v1 > v2 ? 1 : 0;
}

function matches(item: string, text: string, pipe: PipeTransform) {
  const term = text.toLowerCase();
  return item.includes(term);
}
@Component({
  selector: "angular-file-uploader",
  templateUrl: "./angular-file-uploader.component.html",
  styleUrls: ["./angular-file-uploader.component.scss"],
  providers: [UploaderService]
})
export class AngularFileUploaderComponent implements OnInit, OnChanges {
  @Input()
  config: any = {};
  @Input()
  resetUpload: boolean = this.config["resetUpload"];
  @Output()
  ApiResponse = new EventEmitter();

  faTimes = faTimes;
  faFolderOpen = faFolderOpen;
  faPaperPlane = faPaperPlane;
  faSearch = faSearch;
  theme: string;
  id: number;
  hideProgressBar: boolean;
  maxSize: number;
  uploadAPI: string;
  formatsAllowed: string;
  formatsAllowedArray: Array<string> = [];
  multiple: boolean;
  headers: any;
  hideResetBtn: boolean;
  hideSelectBtn: boolean;
  private selectedFiles: Array<any> = [];
  private notAllowedList: Array<Object> = [];
  idDate: number = +new Date();
  reg: RegExp = /(?:\.([^.]+))?$/;
  Caption: Array<string> = [];
  singleFile = true;
  progressBarShow = false;
  uploadBtn = false;
  uploadMsg = false;
  afterUpload = false;
  uploadClick = true;
  uploadMsgText: string;
  uploadMsgClass: string;
  percentComplete: number;
  replaceTexts;
  dropfire = false;
  fileName: string = 'Nenhum arquivo selecionado';

  total: number = 0;
  private _loading$ = new BehaviorSubject<boolean>(true);
  private _searchFiles$ = new Subject<void>();
  private _selectedFiles$ = new BehaviorSubject<Array<any>>([]);
  private _uploadedFiles$ = new BehaviorSubject<Array<UploadResult>>([]);
  private _notAllowedList$ = new BehaviorSubject<Array<Object>>([]);
  private _searchTerm: string = '';


  constructor(private httpClient: HttpClient,
              private uploaderService: UploaderService,
              private toastr: ToastrService) {
    //console.log("id: ",this.id);
    //console.log("idDate: ",this.idDate);
    //console.log(Math.random());
    this.uploaderService.selectedFiles$.subscribe(result => {
      if (result.length !== 0) {
        this.uploadBtn = true;
        if (this.theme == "attachPin") this.uploadFiles();
        else if (result.length==1){
         // this.toastr.info(`O arquivo ${result[0].name} foi preparado para envio!`, "InformaÃ§Ã£o");
        }else{    
         // this.toastr.info(`${result.length}Âº arquivo foi preparado para envio!`, "InformaÃ§Ã£o");
        }
      } else {
        this.uploadBtn = false;
      // this.toastr.info(`Nenhum arquivo foi selecionado para envio!`, "InformaÃ§Ã£o"); 
      }
      this.selectedFiles = result;
      this._searchFiles$.next();
    }); 

    uploaderService.notAllowedList$.subscribe(result => {
      this.notAllowedList = result;
      this._searchFiles$.next();
    });
    uploaderService.uploadedFiles$.subscribe(result => {
      this._uploadedFiles$.next(result);
    });
    uploaderService.total$.subscribe(result => {
      this.total=result;
    });
    uploaderService.progressLoading$.subscribe(result => {
      this.progressBarShow = result;
    });
    uploaderService.percentComplete$.subscribe(result => {
        this.percentComplete= result;
    });
    uploaderService.progressMessage$.subscribe(result => {
      if (result['type'] !=null && result['msg'] !=null)
        this.toastr[result['type']](result['msg']);
    });

    this._searchFiles$.pipe(
      tap(() => this._loading$.next(true)),
      debounceTime(200),
      switchMap(() => this._searchFiles()),
      delay(200),
      tap(() => this._loading$.next(false))
    ).subscribe(result => {
      //this.total = result.selectedItems.length + result.notAllowedItems.length;
      this._notAllowedList$.next(result.notAllowedItems);
      this._selectedFiles$.next(result.selectedItems);
      if (this.theme !== "attachPin" && result.selectedItems)
        if (result.selectedItems.length > 1)
          this.fileName = `${result.selectedItems.length} arquivos selecionados`;
        else if (result.selectedItems.length == 1)
          this.fileName = result.selectedItems[0].name;
        else
          this.fileName = 'Nenhum arquivo selecionado';
      uploaderService.resetTotalItems();
    });
    this._searchFiles$.next();
  }

  get selectedFiles$() { return this._selectedFiles$.asObservable(); }
  get notAllowedList$() { return this._notAllowedList$.asObservable(); }
  get searchFiles$() { return this._searchFiles$.asObservable(); }
  get loading$() { return this._loading$.asObservable(); }
  //get total$() { return this._total$.asObservable(); }
  get searchTerm() { return this._searchTerm; }
  set searchTerm(searchTerm: string) {
    this._searchTerm = searchTerm;
    this._searchFiles$.next();
  }

  private updateSelectedFiles(selectedFiles: Array<any>){
    this._selectedFiles$.next(selectedFiles);
      if (this.theme !== "attachPin" && selectedFiles)
        if (selectedFiles.length > 1)
          this.fileName = `${selectedFiles.length} arquivos selecionados`;
        else if (selectedFiles.length == 1)
          this.fileName = selectedFiles[0].name;
        else
          this.fileName = 'Nenhum arquivo selecionado';
  }
  private _searchFiles(): Observable<SearchResult> {

    // 1. join
    let selectedItems: Array<any> = this.selectedFiles;
    let notAllowedItems: Array<Object> = this.notAllowedList;

    // 2. filter
    selectedItems = selectedItems.filter(item => matches(item.name, this.searchTerm, null));
    notAllowedItems = notAllowedItems.filter(item => matches(item['fileName'], this.searchTerm, null));

    return of({ selectedItems, notAllowedItems });

  }

  ngOnChanges(rst: SimpleChanges) {
    if (rst["config"]) {
      this.theme = this.config["theme"] || "";
      this.id =
        this.config["id"] ||
        parseInt((this.idDate / 10000).toString().split(".")[1]) +
        Math.floor(Math.random() * 20) * 10000;
      this.hideProgressBar = this.config["hideProgressBar"] || false;
      this.hideResetBtn = this.config["hideResetBtn"] || false;
      this.hideSelectBtn = this.config["hideSelectBtn"] || false;
      this.maxSize = this.config["maxSize"] || 20;
      this.uploadAPI = this.config["uploadAPI"]["url"];
      this.formatsAllowed =
        this.config["formatsAllowed"] || ".jpg,.png,.pdf,.docx,.txt,.gif,.jpeg";
      this.multiple = this.config["multiple"] || false;
      this.headers = this.config["uploadAPI"]["headers"] || {};
      let defaultReplaceTextsValues: ReplaceTexts = {
        selectFileBtn: this.multiple ? 'Select Files' : 'Select File',
        resetBtn: 'Reset',
        uploadBtn: 'Upload',
        dragNDropBox: 'Drag N Drop',
        attachPinBtn: this.multiple ? 'Attach Files...' : 'Attach File...',
        afterUploadMsg_success: 'Successfully Uploaded !',
        afterUploadMsg_error: 'Upload Failed !',
        searchFilePlaceHolder: 'Search file',
        loadingMsg: 'Loading!'
      };
      this.replaceTexts = { ...defaultReplaceTextsValues };
      if (this.config["replaceTexts"]) {
        this.replaceTexts = {
          ...defaultReplaceTextsValues,
          ...this.config['replaceTexts']
        }
      }
      this.formatsAllowedArray = this.formatsAllowed.split(",");

      //console.log("config: ", this.config);
      //console.log(this.config["maxSize"]);
      //console.log(this.headers);
      //console.log("rst: ", rst);
    }

    if (rst["resetUpload"]) {
      if (rst["resetUpload"].currentValue === true) {
        this.resetFileUpload();
      }
    }
  }

  ngOnInit() {
    //console.log("Id: ", this.id);
    this.resetUpload = false;
  }

  resetFileUpload() {
    this.selectedFiles = [];
    this.Caption = [];
    this.notAllowedList = [];
    this.uploadMsg = false;
    this.uploadBtn = false;
    this._searchFiles$.next();
  }  
  prepareFile(content: File): void {
    this.uploaderService.includeSelectedFile(content);
  }

  uploadSelectedFiles(): void {
    this.uploaderService.uploadSelected();
    this.toastr.success(`Os arquivos foram enviados com sucesso`);
  }

  public async uploadFilesExtra( ) : Promise<UploadResult> {
      let fileNames = []
      for (let i = 0; i < this.selectedFiles.length; i++) {
        //FileÂ {name: "Captura de Tela 2019-06-27 aÌs 09.28.43.png", lastModified: 1561638579181, lastModifiedDate: Thu Jun 27 2019 09:29:39 GMT-0300 (HorÃ¡rio PadrÃ£o de BrasÃ­lia), webkitRelativePath: "", size: 342370,Â â¦}
        fileNames.push(this.selectedFiles[i].name);
      }
      const options = {
                      headers: this.headers,
                      params: new HttpParams()
                          .set('folder', 'in')
                          .set('fileNames', fileNames[0])};

      var result = await this.httpClient
          .post<ApiUploadResult>(
              this.uploadAPI,
              this.selectedFiles[0], // Send the File Blob as the POST body.
              options
          ).toPromise()
      ;

      return({
          name: fileNames[0],
          type: 'teste',
          size: 0,
          url: ''
      });

  }

  onChange(event: any) {
    //console.log(this.maxSize + this.formatsAllowed + this.multiple);
    //this.notAllowedList = [];
    //console.log("onchange hit");
    if (this.uploaderService.afterUpload || !this.multiple) {
      this.uploaderService.afterUploadAction();
      this.Caption = [];
    }else{
      this.uploaderService.resetNotAllowedList();
    }
    //FORMATS ALLOWED LIST
    //console.log("FORMATS ALLOWED LIST= "+this.formatsAllowed);
    //NO OF FORMATS ALLOWED
    let formatsCount: any;
    formatsCount = this.formatsAllowed.match(new RegExp("\\.", "g"));
    formatsCount = formatsCount.length;
    //console.log("NO OF FORMATS ALLOWED= "+formatsCount);
    //console.log("-------------------------------");

    //ITERATE SELECTED FILES
    let file: FileList;
    if (event.type == "drop") {
      file = event.dataTransfer.files;
      //console.log("type: drop");
    } else {
      file = event.target.files || event.srcElement.files;
      //console.log("type: change");
    }
    //console.log(file);
    let currentFileExt: any;
    let ext: any;
    let frmtAllowed: boolean;
    for (let i = 0; i < file.length; i++) {
      //CHECK FORMAT
      //CURRENT FILE EXTENSION
      currentFileExt = this.reg.exec(file[i].name);
      currentFileExt = currentFileExt[1];
      //console.log(file[i].name);
      frmtAllowed = false;
      //FORMAT ALLOWED LIST ITERATE
      for (let j = formatsCount; j > 0; j--) {
        ext = this.formatsAllowed.split(".")[j];
        //console.log("FORMAT LIST ("+j+")= "+ext.split(",")[0]);
        if (j == formatsCount) {
          ext = this.formatsAllowed.split(".")[j] + ",";
        } //check format
        if (currentFileExt.toLowerCase() == ext.split(",")[0]) {
          frmtAllowed = true;
        }
      }

      if (frmtAllowed) {
        //console.log("FORMAT ALLOWED");
        //CHECK SIZE
        if (file[i].size > this.maxSize * 1024000) {
          //console.log("SIZE NOT ALLOWED ("+file[i].size+")");
          this.uploaderService.includeNotAllowed({
            fileName: file[i].name,
            fileSize: this.convertSize(file[i].size),
            errorMsg: "Tamanho invÃ¡lido"
          });
          this.toastr.error(`Arquivo ${file[i].name} Ã© maior que ${this.convertSize(this.maxSize * 1024000)}!`, "Tamanho invÃ¡lido");

          continue;
        } else {
          //format allowed and size allowed then add file to selectedFile array
          this.uploaderService.includeSelectedFile(file[i]);
        }
      } else {
        //console.log("FORMAT NOT ALLOWED");
        this.uploaderService.includeNotAllowed({
          fileName: file[i].name,
          fileSize: this.convertSize(file[i].size),
          errorMsg: "Formato invÃ¡lido"
        });
        this.toastr.error(`O formato ${currentFileExt} utilizado no arquivo ${file[i].name} nÃ£o Ã© permitido!`, "Formato invÃ¡lido");

        continue;
      }
    }
    this.uploadMsg = false;
    this.uploadClick = true;
    this.percentComplete = 0;
  }

  uploadFiles() {
    //console.log(this.selectedFiles);

    let i: any;
    this.progressBarShow = true;
    this.uploadClick = false;
    this.notAllowedList = [];
    let isError = false;

    let xhr = new XMLHttpRequest();
    const fd: FormData = new FormData();
    fd.append('folder', 'in');
    //fd.append('files', this.selectedFiles);
    //fd.append('files', new Blob(this.selectedFiles[0]));

    let formData = {"":"in", "files": this.selectedFiles[0].name};
    for (i = 0; i < this.selectedFiles.length; i++) {
      if (this.Caption[i] == undefined)
        this.Caption[i] = "file" + i;
      //Add DATA TO BE SENT

      //FileÂ {name: "Captura de Tela 2019-06-27 aÌs 09.28.43.png", lastModified: 1561638579181, lastModifiedDate: Thu Jun 27 2019 09:29:39 GMT-0300 (HorÃ¡rio PadrÃ£o de BrasÃ­lia), webkitRelativePath: "", size: 342370,Â â¦}
      //formData["files"].push({"name":this.selectedFiles[i].name,"size":this.selectedFiles[i].size});
      //console.log(this.selectedFiles[i]+"{"+this.Caption[i]+" (Caption)}");
    }

    if (i > 1) {
      this.singleFile = false;
    } else {
      this.singleFile = true;
    }

    xhr.onreadystatechange = evnt => {
      //console.log("onready");
      if (xhr.readyState === 4) {
        if (xhr.status !== 200 && xhr.status !== 201) {
          isError = true;
          this.progressBarShow = false;
          this.uploadBtn = false;
          this.uploadMsg = true;
          this.afterUpload = true;
          this.uploadMsgText = this.replaceTexts.afterUploadMsg_error;
          this.uploadMsgClass = "text-danger lead";
          this.toastr.error("Ocorreu uma falha durante o envio dos arquivos ao servidor!", this.replaceTexts.afterUploadMsg_error);
          //console.log(this.uploadMsgText);
          //console.log(evnt);
        }
        this.ApiResponse.emit(xhr);
      }
    };

    xhr.upload.onprogress = evnt => {
      this.uploadBtn = false; // button should be disabled by process uploading
      if (evnt.lengthComputable) {
        this.percentComplete = Math.round((evnt.loaded / evnt.total) * 100);
      }
      //console.log("Progress..."/*+this.percentComplete+" %"*/);
    };

    xhr.onload = evnt => {
      //console.log("onload");
      //console.log(evnt);
      this.progressBarShow = false;
      this.uploadBtn = false;
      this.uploadMsg = true;
      this.afterUpload = true;
      if (!isError) {
        this.uploadMsgText = this.replaceTexts.afterUploadMsg_success;
        this.uploadMsgClass = "text-success lead";
        this.toastr.success(`O envio de ${this.selectedFiles.length} arquivos foi finalizado com sucesso!`, this.uploadMsgText)
        //console.log(this.uploadMsgText + " " + this.selectedFiles.length + " file");
      }
    };

    xhr.onerror = evnt => {
      //console.log("onerror");
      //console.log(evnt);
    };
    let url = this.uploadAPI + "?" + Object.keys(formData).map(function (key) { return key + "=" + encodeURIComponent(formData[key]) }).join("&");
    xhr.open("POST", url, true);
    for (const key of Object.keys(this.headers)) {
      // Object.keys will give an Array of keys
      xhr.setRequestHeader(key, this.headers[key]);
    }
    //let token = sessionStorage.getItem("token");
    //xhr.setRequestHeader("Content-Type", "text/plain;charset=UTF-8");
    //xhr.setRequestHeader('Authorization', `Bearer ${token}`);
    xhr.send(new Blob(this.selectedFiles));
  }

  removeFile(i: any, sf_na: any) {
    //console.log("remove file clicked " + i)
    if (sf_na == "sf") {
      this.selectedFiles.splice(i, 1);
      this.Caption.splice(i, 1);
    } else {
      this.notAllowedList.splice(i, 1);
    }

    if (this.selectedFiles.length == 0) {
      this.uploadBtn = false;
    }

    this._searchFiles$.next();
  }

  convertSize(fileSize: number) {
    //console.log(fileSize + " - "+ str);
    return fileSize < 1024000
      ? (fileSize / 1024).toFixed(2) + " KB"
      : (fileSize / 1024000).toFixed(2) + " MB";
  }

  attachpinOnclick() {
    //console.log("ID: ", this.id);
    document.getElementById("sel" + this.id)!.click();
    //$("#"+"sel"+this.id).click();
  }

  drop(event: any) {
    event.stopPropagation();
    event.preventDefault();
    this.dropfire = false;
    //console.log("drop: ", event);
    //console.log("drop: ", event.dataTransfer.files);
    this.onChange(event);
  }
  allowDrop(event: any) {
    event.stopPropagation();
    event.preventDefault();
    event.dataTransfer.dropEffect = "copy";
    this.dropfire = true;
    //console.log("allowDrop: ",event)
  }
  dragLeave(event: any) {
    this.dropfire = false;
    //console.log("dragLeave: ",event)
  }
}


