interface ReplaceTexts {
  selectFileBtn: string,
  resetBtn: string,
  uploadBtn: string,
  dragNDropBox: string,
  attachPinBtn: string,
  afterUploadMsg_success: string,
  afterUploadMsg_error: string,
  searchFilePlaceHolder: string,
  loadingMsg: string
};

interface SearchResult {
  selectedItems: Array<any>;
  notAllowedItems: Array<Object>;
}

interface ApiUploadResult {
    url: string;
}

export interface UploadResult {
    name: string;
    type: string;
    size: number;
    url: string;
}
