import { Component, OnInit, QueryList, ViewChildren, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Form } from  '@angular/forms';
import { NgbModal, ModalDismissReasons} from '@ng-bootstrap/ng-bootstrap';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { Observable } from 'rxjs';
import { UpdownloadService } from './updownload.service';
import { Arquivo } from './models/arquivo.model';
import { NgbdSortableHeader, SortEvent } from './directives/sortable.directive';
import { faDownload, faUpload, faPlus, faTrashAlt, faSearch, faTimesCircle, faPaperPlane, faFile, faFilePdf, faFileWord, faFilePowerpoint, faFileExcel, faFileCsv, faFileArchive, faFileCode, faFileAlt, IconDefinition, faFileImage } from '@fortawesome/free-solid-svg-icons';
import { AngularFileUploaderComponent, UploadResult } from './file-uploader/angular-file-uploader.component';
import { release } from '../environments/release';

@Component({
  selector: 'lib-external-updownload',
  templateUrl: './updownload.component.html',
  styleUrls: ['./updownload.component.scss'],
  providers: [UpdownloadService]
})
export class UpdownloadComponent implements OnInit {
  title = '';
  faPlus = faPlus;
  faDownload = faDownload;
  faUpload = faUpload;
  faTrashAlt = faTrashAlt;
  faSearch = faSearch;
  loading$: Observable<boolean>;
  downloadedItems$: Observable<Arquivo[]>;
  uploadedItems$: Observable<Arquivo[]>;
  total$: Observable<number>;

  progressLoading: boolean = false;
  percentComplete: number = 0;

  token: string = "";
  resetVar: string = "";
  afuConfig = {
    multiple: true,
    formatsAllowed: ".jpg,.png,.pdf,.xls,.csv,.doc,.docx,.txt,.zip",
    maxSize: "10",
    uploadAPI:  {
      url:"/conppwPadraoC9-war/ArquivoUploadServlet",
      
    },
    theme: "dragNDrop",
    hideProgressBar: false,
    hideResetBtn: false,
    hideSelectBtn: false,
    replaceTexts: {
      selectFileBtn: 'Selecionar',
      resetBtn: 'Limpar',
      uploadBtn: 'Enviar',
      dragNDropBox: 'selecione seus arquivos ou arraste eles pra dentro da linha pontilhada!',
      attachPinBtn: 'Incluir anexos...',
      afterUploadMsg_success: 'Arquivos enviados com sucesso !',
      afterUploadMsg_error: 'Falha ao enviar arquivos !',
      searchFilePlaceHolder:'Pesquisar por nome',
      loadingMsg:'Carregando!'
    }
  };

  uploadForm: Form;
  uploadFormGroup: FormGroup;
  downloadForm: FormGroup;
  uploadFormData: FormData;
  uploadFileReader: FileReader;

  error: string;
  userId: string = "rcvry";
  uploadResponse = { status: '', message: '', filePath: '' };
  selectedFile = false;
  selectedFileName = "";
  static LOCALE_KEY = 'currentLocale';

  release: string = release.version;

  @ViewChildren(NgbdSortableHeader) headers: QueryList<NgbdSortableHeader>;
  @ViewChild('uploadZone')
  private uploadZone:  AngularFileUploaderComponent;
  constructor(private formBuilder: FormBuilder,
              private updownloadService: UpdownloadService,
              private modalService: NgbModal,
              private toastr: ToastrService,
              public translate: TranslateService) {
    
    //translate.addLangs(['pt','en']);
    translate.setDefaultLang('ptBR');
    const currentLocale =  sessionStorage.getItem(UpdownloadComponent.LOCALE_KEY);
        
    translate.setDefaultLang(currentLocale);
    this.uploadedItems$ = this.updownloadService.uploadedItems$;
    this.downloadedItems$ = updownloadService.downloadedItems$;
    this.total$ = updownloadService.total$;
    this.loading$ = updownloadService.loading$;
    updownloadService.progressLoading$.subscribe(result => {
      this.progressLoading = result;
    });
    updownloadService.percentComplete$.subscribe(result => {
        this.percentComplete= result;
    });
    updownloadService.progressMessage$.subscribe(result => {
      if (result['type'] !=null && result['msg'] !=null)
        this.toastr[result['type']](result['msg']);
    });
  }

  get page() { return this.updownloadService.page; }
  get pageSize() { return this.updownloadService.pageSize; }
  get searchTerm() { return this.updownloadService.searchTerm; }
  set page(page: number) { this.updownloadService.page =  page;}
  set pageSize(pageSize: number) { this.updownloadService.pageSize =  pageSize; }
  set searchTerm(searchTerm: string) {  this.updownloadService.searchTerm = searchTerm; }

  onSort({ column, direction }: SortEvent) {
    // resetting other headers
    this.headers.forEach(header => {
      if (header.sortable !== column) {
        header.direction = '';
      }
    });

    // sorting items
    this.updownloadService.sortColumn = column;
    this.updownloadService.sortDirection = direction;
  }

  ngOnInit() {
    this.uploadFormGroup = this.formBuilder.group({
      uploads: ['']
    },{
      downloads: ['']
    });
  }


  clear(): void {
    this.searchTerm = "";
    this.headers.forEach(header => {
        header.direction = '';
    });
    this.uploadZone.resetFileUpload();
  }

  private getDismissReason(reason: any): string {
      if (reason === ModalDismissReasons.ESC) {
        return 'by pressing ESC';
      } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
        return 'by clicking on a backdrop';
      } else {
        return  `with: ${reason}`;
      }
  }

  listarArquivos(): void {

  }

  onUploadFileChange(event) {
    if (event.target.files.length > 0) {
      const file = event.target.files[0];
      this.selectedFile = true;
      this.selectedFileName = event.target.val().split("\\").pop();
      this.uploadFormGroup.get('uploads').setValue(file);
    }
  }

  onDownloadFileChange(event) {
    if (event.target.files.length > 0) {
      const file = event.target.files[0];
      this.downloadForm.get('downloads').setValue(file);
    }
  }

  downloadFile(content: Arquivo): void {
    this.updownloadService.downloadItem(content);
  }

}
